import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
	{
		path: '/', component: {render: h => h('router-view')},
		children: [
			{path: '', name: "Main", component: () => import('../views/Main.vue')}, // 서비스 메인


		]
	},
]

const router = new VueRouter({
	mode: 'history', // History mode 설정 #을 없애고 history 를 사용할 수 있다
	hash: true,
	routes
})


export default router
