    <transition name="modal">
        <div class="empty_modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container modal-background-color" :style="{padding:0}">

                    <div class="common-message-dialog-title">
                        {{ $store.state.commonMessage.title }}
                    </div>

                    <!-- Customer Area -->
                    <div class="common-message-dialog-body">
                        <!-- Deploy Method -->
                        {{ $store.state.commonMessage.message }}
                    </div>
                    <!-- Button Footer -->
                    <div class="modal-footer">
                        <button  @click="clickFinish()">OK</button>
                    </div>

                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import {hideCommonMessageDialog} from "@/utils/uiHelper";

export default {
    name: "CommonMessagePopup",

    data() {
        return {
        }
    },

    methods: {
        clickFinish() {
            // alert(this.$store.state.commonMessage.callback)
            if (this.$store.state.commonMessage.callback !== undefined) {
                this.$emit(this.$store.state.commonMessage.callback)
            } else {
                // this.$emit('finish', this.BINDING.quizGroup)
                hideCommonMessageDialog(this);
            }
        }
    }


}
</script>

