/**
 * 화면 사이즈 등을 처리하기 위한 Util
 * @type {{getModel: (function(): *), setModel: storeHelper.setModel}}
 */

// 화면의 전체 높이를 구한다.
export function getScreenHeightToPixel() {
    let full = document.body.scrollHeight

    console.log(full)
    return full + 'px';
}

/**
 * 하단 공통 notice 팝업
 * @param _this
 * @param message
 */
export function showBottomDialog(_this, message) {
    _this.$store.commit('showAutoFadeOutDialog', message);

    // 3초 뒤 자동 닫힘
    setTimeout(function() {
        _this.$store.commit('hideAutoFadeOutDialog');
    }, 1000 * 3);

}

export function showCommonMessageDialog(_this, _title, _message, callback) {
    _this.$store.commit('showCommonMessageDialog', {title:_title, message:_message}, callback);

}

export function hideCommonMessageDialog(_this) {
    _this.$store.commit('hideCommonMessageDialog');
}
