/* store.js */
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		showDialog: false,
		message: 'message',

		commonMessage: {
			showCommonDialog: false,
			title: '',
			message: '',
			// callback: ''
		},
		model:{}
	},
	mutations: {
		showAutoFadeOutDialog(state, message) {

			state.showDialog = true
			state.message = message;
		},

		hideAutoFadeOutDialog(state) {
			state.showDialog = false;
			state.message = '';
		},

		// Common Message Popup
		showCommonMessageDialog(state, payload) {

			// console.log(callback)
			state.commonMessage.showCommonDialog = true;
			state.commonMessage.title = payload.title;
			state.commonMessage.message = payload.message;
			// state.commonMessage.callback = callback;
		},

		hideCommonMessageDialog(state) {
			state.commonMessage.showCommonDialog = false;
			state.commonMessage.title = '';

			state.commonMessage.message = '';
		},
		setMode(state, obj) {
			state.model = obj
		}
	},

	getters: {
		getModel() {
			return this.state.model;
		}
	}
})